.toolbar {
  &_static {
    .toolbar {
      &__col {
        vertical-align: middle;
        padding: 0 10px;
        height: 50px; } } }
  &_delimited {
    border-bottom: 1px solid rgba(0, 0, 0, 0.1); }
  &__container {
    display: table;
    table-layout: fixed;
    width: 100%; }
  &__row {
    display: table-row; }
  &__col {
    display: table-cell;
    vertical-align: top;
    font-size: 16px;
    &:not(:last-child) {
      border-right: 1px solid rgba(0, 0, 0, 0.1); }
    // &_lg
    //   width: 55%
    // &_sm
    //   width: 45%
    // &_lg,
    // &_sm
    //   +lg
    //     width: 50%
    &_refresh {
      // width: 128px
      width: 150px; } }
  &__button {
    padding: 0 20px;
    text-align: left;
    @include clr;
    @include md {
      font-size: 16px; }
    i,
    strong {
      float: right; }
    i {
      margin-right: -10px;
      height: auto;
      line-height: inherit; }
    strong {
      @include rreg;
      font-size: 25px;
      @include lg {
        font-size: 19px; }
      @include md {
        font-size: 16px; } } }
  &__refresh {
    height: 80px; } }

